//Footer
.footer
	background: linear-gradient(162.03deg, #040213 0.72%, #030112 87.27%)

	&._about
		background: transparent

		*
			color: rgba(41, 38, 68, 1)

		.footer__copy i
			color: #008000

	&__content
		display: flex
		@include adaptiveValue('padding-bottom', 111, 20, 1)
		@media (max-width: 1024px)
			flex-wrap: wrap
			@include adaptiveValue('gap', 45, 20, 1)

	&__left
		max-width: 265px
		width: 100%
		@include adaptiveValue('margin-right', 168, 0, 1)
		@media (max-width: 1024px)
			max-width: 100%
			display: flex
			justify-content: space-between
			align-items: center
			flex-wrap: wrap
			order: 2
			margin-right: 0
		@media (max-width: 420px)
			justify-content: space-between
			text-align: center
		@media (max-width: 320px)
			justify-content: center

	&__left-item
		@media (max-width: 420px)
			order: 2
			margin: 0 0 30px

	&__left-item:nth-child(2)
		@media (max-width: 1024px)
			text-align: right
		@media (max-width: 420px)
			text-align: center
			order: 1

	&__logo
		display: inline-block
		@extend .header__logo
		margin: 0 0 20px

	&__copy
		display: flex
		align-items: center
		font-weight: 500
		font-size: 17px
		white-space: nowrap
		@include adaptiv-font(17, 13, 1)
		line-height: 164%
		color: #292644
		margin: 0 0 43px

		i
			font-style: normal
			font-weight: bold
			text-transform: uppercase
			color: #008000
			padding: 0 0 0 5px

		@media (max-width: 1024px)
			margin: 0

	&__title
		font-weight: 800
		//font-size: 24px
		@include adaptiv-font(24, 16, 1)
		line-height: 148%
		color: #FFFFFF
		margin: 0 0 8px

	&__link-email
		display: inline-block
		font-weight: 500
		//font-size: 17px
		@include adaptiv-font(17, 13, 1)
		line-height: 164%
		color: #FFFFFF
		transition: all .4s ease 0s
		@media (any-hover: hover)

		&:hover
			color: rgba(63, 188, 98, .44)

	&__right
		//@media (max-width: 1024px)
		width: 100%

	&__nav
		display: flex
		align-items: center
		justify-content: space-between
		//@include adaptiveValue('column-gap', 123, 15, 1)
		@media (max-width: 1024px)
			width: 100%
			justify-content: space-evenly
		@media (max-width: 420px)
			flex-direction: column
			text-align: center
			//gap: 20px
			@include adaptiveValue('gap', 20, 0, 1)

	&__nav-title
		position: relative
		display: inline-block
		font-weight: 800
		//font-size: 24px
		@include adaptiv-font(24, 18, 1)
		line-height: 148%
		color: #FFFFFF
		//margin: 0 0 29px
		@include adaptiveValue('margin-bottom', 29, 15, 1)

		&._active .footer__nav-title-arrow
			transform: rotate(180deg)
		transition: all .4s ease 0s
		@media (any-hover: hover)
			&:hover
				color: rgba(63, 188, 98, .44)

	&__nav-title-arrow
		position: absolute
		top: 7px
		right: -28px
		width: 15px
		height: 15px
		display: none
		justify-content: center
		align-items: center
		transition: all .4s ease 0s

		span
			position: relative
			height: 5px

			&:after
				position: absolute
				content: ''
				top: 0
				right: 2px
				width: 1px
				height: 8px
				transform: rotate(-45deg)
				background: #FFFFFF

			&:before
				position: absolute
				content: ''
				top: 0
				left: 2px
				width: 1px
				height: 8px
				transform: rotate(45deg)
				background: #FFFFFF

		@media (max-width: 420px)
			display: flex

	&__nav-item
		//&:not(:last-child)
			//margin: 0 0 30px
		@include adaptiveValue('margin-bottom', 30, 15, 1)
		@media (max-width: 420px)
			text-align: center

	&__nav-link
		font-weight: 500
		//font-size: 17px
		@include adaptiv-font(17, 14, 1)
		line-height: 164%
		color: #292644
		transition: all .4s ease 0s
		@media (any-hover: hover)

		&:hover
			color: rgba(63, 188, 98, .44)
