//Accordion
.accordion
	max-width: 50em
	width: 100%

	*
		font-size: 1.4375rem

	&__content
		position: relative
		margin: 1.25em

	&__label
		position: relative
		cursor: pointer
		color: #fff
		background: purple
		padding: 0.625em

		&::before
			position: absolute
			content: '+'
			top: 50%
			right: .625rem
			transform: translateY(-50%)
			font-size: 1.5rem


		&._active + .accordion__body
			height: 9.375rem
			padding: 0.625em

		&._active::before
			content: '-'

	&__body
		position: relative
		height: 0
		overflow: hidden
		overflow-y: auto
		color: white
		background: blue

		@media (-ms-high-contrast: active), (-ms-high-contrast: none)
			scrollbar-face-color: purple
			scrollbar-track-color: blue
			scrollbar-arrow-color: blue

		&::-webkit-scrollbar
			width: .5125rem

		&::-webkit-scrollbar-track
			background: $neutral-placeholder

		&::-webkit-scrollbar-thumb
			border-radius: calc(10px / 2)
			background-clip: padding-box
			background-color: rgba($colorC, 1)
