@use 'sass:math';
//Подключение шрифта
@mixin font($font_name, $file_name, $weight, $style) {
	@font-face {
		font-family: $font_name;
		font-display: swap;
		src: url("../fonts/#{$file_name}.woff2") format("woff2"),
		url("../fonts/#{$file_name}.woff") format("woff");
		font-weight: #{$weight};
		font-style: #{$style};
	}
}

@function proc($pxOne, $pxTwo) {
	$result: math.div($pxOne, $pxTwo) * 100%;
	@return $result;
}

@function rem($px) {
	$result: math.div($px, 16) + rem;
	@return $result;
}

@function em($px) {
	$result: math.div($px, 16) + em;
	@return $result;
}

@mixin fontFull($family, $weight, $px, $color) {
	font-family: $family, sans-serif;
	font-weight: $weight;
	font-size: rem($px);
	color: $color;
}

@mixin fontMedium($weight, $px, $color) {
	font-weight: $weight;
	font-size: rem($px);
	color: $color;
}

@mixin AP_full($var0, $var1, $var2, $var3, $var4, $var5, $var6, $var7) {
	@include adaptiv-value('padding-top', $var0, $var1, 1);
	@include adaptiv-value('padding-bottom', $var2, $var3, 1);
	@include adaptiv-value('padding-left', $var4, $var5, 1);
	@include adaptiv-value('padding-right', $var6, $var7, 1);
}

@mixin AP_t-b($var0, $var1, $var2, $var3) {
	@include adaptiv-value('padding-top', $var0, $var1, 1);
	@include adaptiv-value('padding-bottom', $var2, $var3, 1);
}

@mixin AP_l-r($var4, $var5, $var6, $var7) {
	@include adaptiv-value('padding-left', $var4, $var5, 1);
	@include adaptiv-value('padding-right', $var6, $var7, 1);
}

@mixin AP_top($var0, $var1) {
	@include adaptiv-value('padding-top', $var0, $var1, 1);
}

@mixin AP_bottom($var2, $var3) {
	@include adaptiv-value('padding-bottom', $var2, $var3, 1);
}

@mixin AP_left($var4, $var5) {
	@include adaptiv-value('padding-left', $var4, $var5, 1);
}

@mixin AP_right($var6, $var7) {
	@include adaptiv-value('padding-right', $var6, $var7, 1);
}

@mixin adaptiv-font($pcSize, $mobSize, $maxSize) {
	$addSize: $pcSize - $mobSize;

	@if $maxSize==1 {
		//Только если меньше контейнера
		font-size: $pcSize + px;

		@media (max-width: #{$maxWidthContainer + px}) {
			font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidthContainer - 320}));
		}
	}

	@else if $maxSize==2 {
		//Только если больше контейнера
		font-size: $pcSize + px;

		@media (min-width: #{$maxWidthContainer + px}) {
			font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
		}
	}

	@else {
		//Всегда
		font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
	}
}

@mixin adaptiv-font-import($pcSize, $mobSize, $maxSize) {
	$addSize: $pcSize - $mobSize;

	@if $maxSize==1 {
		//Только если меньше контейнера
		font-size: $pcSize + px;

		@media (max-width: #{$maxWidthContainer + px}) {
			font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidthContainer - 320})) !important;
		}
	}

	@else if $maxSize==2 {
		//Только если больше контейнера
		font-size: $pcSize + px;

		@media (min-width: #{$maxWidthContainer + px}) {
			font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320})) !important;
		}
	}

	@else {
		//Всегда
		font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320})) !important;
	}
}

//Адаптивное свойство
@mixin adaptiv-value($property, $startSize, $minSize, $type) {
	$addSize: $startSize - $minSize;

	@if $type==1 {
		//Только если меньше контейнера
		#{$property}: $startSize + px;

		@media (max-width: #{$maxWidthContainer + px}) {
			#{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidthContainer - 320}));
		}
	}

	@else if $type==2 {
		//Только если больше контейнера
		#{$property}: $startSize + px;

		@media (min-width: #{$maxWidthContainer + px}) {
			#{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
		}
	}

	@else {
		//Всегда
		#{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
	}
}

//Адаптивное свойство
@mixin adaptiv-value-proc($property, $startSize, $minSize, $type) {
	$addSize: $startSize - $minSize;

	@if $type==1 {
		//Только если меньше контейнера
		#{$property}: $startSize + '%';

		@media (max-width: #{$maxWidthContainer + px}) {
			#{$property}: calc(#{$minSize + '%'} + #{$addSize} * ((100vw - 320px) / #{$maxWidthContainer - 320}));
		}
	}

	@else if $type==2 {
		//Только если больше контейнера
		#{$property}: $startSize + '%';

		@media (min-width: #{$maxWidthContainer + px}) {
			#{$property}: calc(#{$minSize + '%'} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
		}
	}

	@else {
		//Всегда
		#{$property}: calc(#{$minSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth - 320}));
	}
}

