//Стили шрифта
@mixin font-source-sans($size: false, $colour: false, $weight: false, $lh: false) {
	font-family: 'Source Sans Pro', Helvetica, Arial, sans-serif;

	@if $size {
		font-size: $size;
	}

	@if $weight {
		font-weight: $weight;
	}

	@if $lh {
		line-height: $lh;
	}

	@if $colour {
		color: $colour;
	}
}

//Медиа-запросы
$breakpoints: ("iPhone": 321px,
	"phone": 400px,
	"iPhone6/7/8/Plus-vertical": 420px,
	"phone-wide": 480px,
	"phablet": 560px,
	"tablet-small": 640px,
	"iPhone6/7/8/Plus-horizontal": 736px,
	"tablet": 769px,
	"tablet-wide": 1024px,
	"desktop": 1248px,
	"desktop-wide": 1440px);

@mixin mq($width, $type: min) {
	@if map_has_key($breakpoints, $width) {
		$width: map_get($breakpoints, $width);

		@if $type==max {
			$width: $width - 1px;
		}

		//@media only screen and (#{$type}-width: $width) {
		@media (#{$type}-width: $width) {
			@content;
		}
	}
}

