//Аппаратные средства
@mixin hardware($backface: true, $perspective: 1000) {
	@if $backface {
		backface-visibility: hidden;
	}

	perspective: $perspective;
}

//Простое и эффективное средство, когда вам нужно включить ускорение с помощью
//аппаратных средств для анимаций, сохраняя все быстрым, гладким и немерцающим.

//Добавить усечение к элементу не так просто, как кажется, хотя это экономит время, когда вы включаете миксин,
//вы должны только указать границу и максимальную ширину. Удачных дней!
@mixin truncate($truncation-boundary) {
	max-width: $truncation-boundary;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

