//Header
.header
	position: fixed
	top: 0
	right: 0
	left: 0
	width: 100%
	background: map-get($bgHeader, 'color')
	z-index: 11

	&__content
		display: flex
		align-items: center
		@include adaptiveValue('padding-top', 50, 10, 1)
		@include adaptiveValue('padding-bottom', 50, 10, 1)

	&__logo
		@include fontFull("Gilroy", 900, 35, #ffffff)
		@include adaptiv-font(35, 25, 1)
		font-style: italic
		cursor: pointer
		z-index: 11
		margin: 0 89px 0 0
		transition: all .4s ease 0s

		b
			color: #5641FF

		@media (any-hover: hover)
			&:hover
				color: #5641FF

	&._hide
		transform: translateY(-100%)
		transition: transform .4s ease-in-out

	&__btn
		display: flex
		justify-content: center
		align-items: center
		width: 240px
		@include adaptiveValue('height', 69, 40, 1)
		@include fontFull("TTFirsNeue", 500, 18, #ffffff)
		@include adaptiv-font(18, 14, 1)
		border: 2px solid transparent
		border-radius: 8px
		background: #5641FF
		z-index: 11

		&:before
			@include adaptiveValue('margin-right', 22, 10, 1)
		transition: all .4s ease 0s
		@media (any-hover: hover)
			&:hover
				border-color: #FFFFFF
				background: transparent
		@media (max-width: 768px)
			margin: 0 55px 0 0
		@media (max-width: 410px)
			display: none

	&.headroom--not-top
		background: rgba(4, 1, 18, 1)

.header__btn_burger
	display: none

.menu__body._nav._active .header__btn_burger
	display: none
	margin: 0 auto
	@media (max-width: 410px)
		display: flex

.menu
	display: flex
	align-items: center
	flex: 1 1 auto
	margin: 0 10px 0 0

	&__list
		display: flex
		align-items: center

	&__item
		span
			cursor: pointer

		&:not(:last-child)
			@include adaptiveValue('margin-right', 60, 0, 1)

	&__link
		position: relative
		@include adaptiv-font(18, 16, 1)
		@include fontFull("TTFirsNeue", 400, 18, rgba(78, 70, 133, .37))
		color: #FFFFFF
		transition: all .4s ease 0s
		@media (any-hover: hover)
			&:hover:before
				opacity: 1
				visibility: visible

		&:before
			position: absolute
			content: ''
			bottom: -2px
			left: 0
			width: 100%
			height: 2px
			background: #FFFFFF
			opacity: 0
			visibility: hidden

		&._link_active
			color: #FFFFFF
