.about-top {
	background: url('../img/about/about-bg.png') no-repeat top center/cover;

	&__content {
		position:        relative;
		display:         flex;
		justify-content: space-between;

		* {
			color: #FFFFFF;
		}

		@include adaptiveValue('padding-top', 258, 150, 1);
		@include adaptiveValue('padding-bottom', 79, 50, 1);
		@media (max-width: 1024px) {
			flex-wrap: wrap;
		}
	}

	&__left {
		max-width: 436px;
		@include adaptiveValue('margin-right', 112, 0, 1);
		@media (max-width: 1024px) {
			max-width: 100%;
			margin: 0 0 25px;
		}
		@media (max-width: 768px) {
			margin: 0 0 80px;
		}
	}

	&__title-items {
		display:     flex;
		align-items: center;
		@include adaptiveValue('margin-bottom', 63, 45, 1);
	}

	&__title {
		font-family: 'TTFirsNeue', sans-serif;
		@include adaptiv-font(43, 20, 1);
		font-weight: 600;
		line-height: 133%;
		@include adaptiveValue('margin-right', 34, 25, 1);
	}

	&__img {
		@include adaptiveValue('width', 73, 35, 1);
		@include adaptiveValue('height', 73, 35, 1);
		display:         flex;
		justify-content: center;
		align-items:     center;
		@include adaptiveValue('border-radius', 15, 7, 1);
		background:      #131030;
		img, picture {
			display: flex;
			justify-content: center;
			align-items: center;
			@media (max-width: 768px) {
				width: 60%;
				height: auto;
			}
		}
	}

	&__btn {
		background: #5641FF;
		transition: all .4s ease 0s;

		span:before {
			transition: all .4s ease 0s;
		}

		@media (any-hover: hover) {
			&:hover {
				border-color: #FFFFFF;
				color:        #FFFFFF;

				span:before {
					color: #FFFFFF
				}
			}
		}
		@media (max-width: 1024px) {
			margin: 0 auto;
		}
	}

	&__text {
		@include adaptiv-font(18, 14, 1);
		line-height: 189%;
		font-weight: 500;
		@include adaptiveValue('margin-bottom', 66, 45, 1);
	}

	&__right {
		position:  absolute;
		right:     0;
		bottom:    -78px;
		max-width: 876px;
		@media (max-width: 1024px) {
			position: static;
			max-width: 100%;
			display: flex;
			flex-direction: column;
			align-items: flex-end;
		}
	}

	&__item {
		position:        relative;
		height:          156px;
		display:         flex;
		align-items:     center;
		border:          2px solid #FFFFFF;
		border-radius:   19px;
		background:      linear-gradient(360deg, rgba(255, 255, 255, 0.88) -7.32%, #FFFFFF 95.88%);
		backdrop-filter: blur(108px);
		@include adaptiveValue('padding-top', 27, 20, 1);
		@include adaptiveValue('padding-right', 55, 15, 1);
		@include adaptiveValue('padding-bottom', 27, 20, 1);
		@include adaptiveValue('padding-left', 80, 60, 1);
		transition:      all .4s ease 0s;

		&:not(:last-child) {
			margin: 0 0 43px;
			@media (max-width: 768px) {
				margin: 0 0 60px !important;
			}
		}

		@media (max-width: 533px) {
			width:  100% !important;
			height: 100%;
			flex-direction: column;
			align-items: normal;
			@include adaptiveValue('padding-top', 27, 40, 1);
			@include adaptiveValue('padding-right', 20, 20, 1);
			@include adaptiveValue('padding-bottom', 20, 20, 1);
			@include adaptiveValue('padding-left', 20, 20, 1);
		}

		@media (any-hover: hover) {
			&:hover {
				transform: translateY(-5px);
			}
			&:hover .about-top__icon {
				transform: translateY(-7px);
			}
		}
		@media (max-width: 1024px) {
			width: 95%;
			height: auto;
			background:      #ffffff;
			backdrop-filter: none;
			box-shadow:      0 0 15px rgba(255, 255, 255, 0.5),
			                 10px 10px 15px rgba(70, 70, 70, 0.12);
			&:not(:last-child) {
				margin: 0 0 25px;
			}
		}


		&:first-child .about-top__subtitle {
			max-width: 180px;
			width:     100%;
		}

		&:nth-child(2) .about-top__subtitle {
			max-width: 106px;
			width:     100%;
		}

		&:last-child .about-top__subtitle {
			max-width: 106px;
			width:     100%;
		}
	}

	&__icon {
		position:        absolute;
		@include adaptiveValue('left', -42, -25, 1);
		@include adaptiveValue('width', 84, 50, 1);
		@include adaptiveValue('height', 84, 50, 1);
		display:         flex;
		justify-content: center;
		align-items:     center;
		border-radius:   50%;
		box-shadow:      0px 25px 68px rgba(253, 215, 81, 0.22);
		background:      #FDD751;
		transition:      all .4s ease 0s;
		img, picture {
			display: flex;
			justify-content: center;
			align-items: center;
			@media (max-width: 768px) {
				width: 60%;
				height: auto;
			}
		}
		@media (max-width: 768px) {
			@include adaptiveValue('top', -42, -25, 1);
			@include adaptiveValue('left', 42, 25, 1);
		}
	}

	&__subtitle {
		display:     block;
		@include adaptiv-font(24, 16, 1);
		font-weight: 800;
		line-height: 148%;
		color:       #090528;
		@include adaptiveValue('margin-right', 68, 35, 1);
	}

	&__item-text {
		@include adaptiv-font(18, 14, 1);
		line-height: 189%;
		font-weight: 500;
		color:       #090528;
	}
}

