@use "sass:math";
// Подключение переменных ==============================================================================================
@import "utils/vars";
// Подключение миксинов ================================================================================================
@import "base/mixins";
@import "utils/mixins";
// Подключение шрифтов =================================================================================================
//&display=swap - добавить при подключении через плагин
@import "utils/fonts";
// Подключение иконочных шрифтов =======================================================================================
@import "utils/icons";
// Шрифт по умолчанию ==================================================================================================
$fontFamily: $mainFontFamily, $font-family-sans-serif;
//$fontSize: rem(map-get($fontSize, 'size')); // где 14(px) - размер шрифта по умолчанию из макета
$fontSize: rem($fontSizeVar); // где 14(px) - размер шрифта по умолчанию из макета
// НАСТРОЙКИ ===========================================================================================================
// Минимальная ширина страницы
$minWidth: 320;
// Ширина полотна (макета)
$maxWidth:                 map-get($mW, 'size');
// Ширина ограничивающего контейнера (0 = нет ограничения)
$maxWidthContainer:        map-get($mWContainer, 'size');
// Общий отступ у контейнера
// (30 = по 15px слева и справа, 0 = нет отступа)
$containerPadding:         map-get($paddingContainer, 'size');
// Тип адаптива:
// 1 = отзывчивость (у контейнера нет брейкпоинтов),
// 2 = по брейк-поинтам (контейнер меняет свою ширину по брейк-поинтам)
$responsiveType: 1;
// Ширина страбатывания первого брейкпоинта
$containerWidth: $maxWidthContainer + $containerPadding;
// Брейк-поинты
$pc: em($containerWidth);   // ПК, ноутбуки, некоторые планшеты в горизонтальном положении
$tablet: em(991.98);        // Планшеты, некоторые телефоны в горизонтальном положении
$mobile: em(767.98);        // Телефоны L
$mobileSmall: em(479.98);   // Телефоны S
// Основной цвет шрифта
$mainColor: map-get($basicColor, 'color');
// Основной цвет фона
$mainBackground: map-get($basicBackground, 'color');
// Обнуление и общие параметры =========================================================================================
@import "base/null";

main ~ :not(footer) {
	display: none;
}

body {
	color: $mainColor;

	&._lock {
		overflow: hidden;
	}
}
// Оболочка ============================================================================================================
.wrapper {
	min-height: 100%;
	display: flex;
	flex-direction: column;
	overflow: hidden;
	background: url('../img/body-bg.jpg') no-repeat center/cover;
	 ._loaded & {
	 }
	* {
		min-width: 0;
	}
}
._page {
	position: relative;
	flex: 1 1 auto;
}

@if ($responsiveType==1) {
	// Отзывчивая
	[class*="__container"] {
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			width: 100%;
			margin: 0 auto;
		}
		@if ($containerPadding>0) {
			padding: 0 rem(math.div($containerPadding, 2));
		}
	}
} @else {
	// По брейк-поинтам
	[class*="__container"] {
		margin: 0 auto;
		@if ($maxWidthContainer>0) {
			max-width: rem($maxWidthContainer);
			width: 100%;
		} @else {
			@if ($containerPadding>0) {
				padding: 0 rem(math.div($containerPadding, 2));
			}
		}
		@media (max-width: $pc) {
			max-width: rem(970);
		}
		@media (max-width: $tablet) {
			max-width: rem(750);
		}
		@media (max-width: $mobile) {
			max-width: none;
			@if ($containerPadding>0 and $maxWidthContainer>0) {
				padding: 0 rem(math.div($containerPadding, 2));
			}
		}
	}
}
._ie [class*="__container"] {
	margin: 0;
}
// Подключение базовых стилей, шаблонов (заготовок) и вспомагательных классов ==========================================
@import "null";
@import "base";
@import "utils/util";
// Подключение стилей статических блоков
@import "static/header";
@import "static/footer";
// Подключение стилей отдельных блоков
@import "../pug/components/top/top";
@import "../pug/components/product/product";
@import "../pug/components/systems/systems";
@import "../pug/components/questions/questions";
@import "../pug/components/about-top/about-top";
@import "../pug/components/about/about";
@import "../pug/components/terms/terms";
// Подключение стилей отдельных страниц
@import "home";
@import "includes/keyframes";
@import "includes/accordion";
