.about {

	&__content {
		@include adaptiveValue('padding-top', 135, 50, 1);
		@include adaptiveValue('padding-bottom', 126, 100, 1)
	}

	&__header {
		display:     flex;
		align-items: center;
		@include adaptiveValue('margin-bottom', 66, 50, 1);
	}

	&__title {
		font-family: 'TTFirsNeue', sans-serif;
		@include adaptiv-font(43, 20, 1);
		font-weight: 600;
		line-height: 133%;
		color:       #07041C;
		@include adaptiveValue('margin-right', 34, 25, 1)
	}

	&__img {
		@include adaptiveValue('width', 73, 35, 1);
		@include adaptiveValue('height', 73, 35, 1);
		display:         flex;
		justify-content: center;
		align-items:     center;
		@include adaptiveValue('border-radius', 15, 7, 1);
		background:      linear-gradient(180deg, rgba(8, 5, 35, 0.06) 0%, rgba(9, 6, 38, 0.05) 100%);
		img, picture {
			display: flex;
			justify-content: center;
			align-items: center;
			@media (max-width: 768px) {
				width: 60%;
				height: auto;
			}
		}
	}

	&__body {
		display:               grid;
		grid-template-columns: repeat(4, 1fr);
		grid-template-rows:    repeat(2, 265px);
		@include adaptiveValue('gap', 37, 25, 1);

		@media (max-width: 1024px) {
			grid-template-columns: repeat(1, 1fr);
			grid-template-rows:    repeat(4, auto);
		}

		.item-1 {
			grid-column: 1;
			grid-row:    1/3;

			.about__subtitle {
				@include adaptiveValue('margin-bottom', 38, 25, 1);
			}

			@media (max-width: 1024px) {
				grid-column: 1/5;
				grid-row:    1/2;
				display:     flex;
				align-items: center;
				@include adaptiveValue('padding-top', 30, 25, 1);
				@include adaptiveValue('padding-right', 20, 15, 1);
				@include adaptiveValue('padding-bottom', 30, 25, 1);
				@include adaptiveValue('padding-left', 20, 15, 1);
				.about__subtitle {
					@include adaptiveValue('margin-bottom', 24, 18, 1);
				}
			}
		}

		.item-2 {
			grid-column: 2;
			grid-row:    1/3;

			.about__subtitle {
				@include adaptiveValue('margin-bottom', 38, 25, 1);
			}

			@media (max-width: 1024px) {
				grid-column: 1/5;
				grid-row:    2/3;
				display:     flex;
				align-items: center;
				@include adaptiveValue('padding-top', 30, 25, 1);
				@include adaptiveValue('padding-right', 20, 15, 1);
				@include adaptiveValue('padding-bottom', 30, 25, 1);
				@include adaptiveValue('padding-left', 20, 15, 1);
				.about__subtitle {
					@include adaptiveValue('margin-bottom', 24, 18, 1);
				}
			}
		}

		.item-3 {
			grid-column: 3/5;
			grid-row:    1;
			display:     flex;
			align-items: center;
			@include adaptiveValue('padding-top', 52, 25, 1);
			@include adaptiveValue('padding-right', 51, 15, 1);
			@include adaptiveValue('padding-bottom', 52, 25, 1);
			@include adaptiveValue('padding-left', 51, 15, 1);
			@media (max-width: 1024px) {
				grid-column: 1/5;
				grid-row:    3/4;
				@include adaptiveValue('padding-top', 30, 25, 1);
				@include adaptiveValue('padding-right', 20, 15, 1);
				@include adaptiveValue('padding-bottom', 30, 25, 1);
				@include adaptiveValue('padding-left', 20, 15, 1);
			}
		}

		.item-4 {
			grid-column: 3/5;
			grid-row:    2;
			display:     flex;
			align-items: center;
			@include adaptiveValue('padding-top', 52, 25, 1);
			@include adaptiveValue('padding-right', 51, 15, 1);
			@include adaptiveValue('padding-bottom', 52, 25, 1);
			@include adaptiveValue('padding-left', 51, 15, 1);
			@media (max-width: 1024px) {
				grid-column: 1/5;
				grid-row:    4/5;
				@include adaptiveValue('padding-top', 30, 25, 1);
				@include adaptiveValue('padding-right', 20, 15, 1);
				@include adaptiveValue('padding-bottom', 30, 25, 1);
				@include adaptiveValue('padding-left', 20, 15, 1);
			}
		}
	}

	&__item {
		border:          2px solid #FFFFFF;
		border-radius:   19px;
		background:      linear-gradient(360deg, rgba(255, 255, 255, 0.88) -7.32%, #FFFFFF 95.88%);
		backdrop-filter: blur(108px);
		@include adaptiveValue('padding-top', 58, 25, 1);
		@include adaptiveValue('padding-right', 51, 15, 1);
		@include adaptiveValue('padding-bottom', 58, 25, 1);
		@include adaptiveValue('padding-left', 51, 15, 1);
		transition:      all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				transform: translateY(-5px);
			}
		}
		@media (max-width: 400px) {
			flex-direction: column;
		}
	}

	&__num {
		@include adaptiveValue('width', 99, 50, 1);
		@include adaptiveValue('height', 99, 50, 1);
		display:         flex;
		justify-content: center;
		align-items:     center;
		flex-shrink:     0;
		font-family:     'TTFirsNeue', sans-serif;
		@include adaptiv-font(49, 20, 1);
		font-weight:     700;
		line-height:     133%;
		border-radius:   50%;
		@media (max-width: 400px) {
			margin: 0 0 25px !important;
		}

		&_one {
			color:      rgba(58, 187, 112, 1);
			background: rgba(81, 253, 150, 0.26);
			@include adaptiveValue('margin-bottom', 38, 25, 1);
			@media (max-width: 1024px) {
				margin-bottom: 0;
				@include adaptiveValue('margin-right', 51, 25, 1);
			}
		}

		&_two {
			color:      rgba(242, 182, 61, 1);
			background: rgba(253, 195, 81, 0.17);
			@include adaptiveValue('margin-bottom', 38, 25, 1);
			@media (max-width: 1024px) {
				margin-bottom: 0;
				@include adaptiveValue('margin-right', 51, 25, 1);
			}
		}

		&_three {
			color:      rgba(255, 49, 173, 1);
			background: rgba(255, 49, 173, 0.18);
			@include adaptiveValue('margin-right', 51, 25, 1);
		}

		&_four {
			color:      rgba(86, 65, 255, 1);
			background: rgba(81, 98, 253, 0.06);
			@include adaptiveValue('margin-right', 51, 25, 1);
		}
	}

	&__subtitle {
		display:     inline-block;
		@include adaptiv-font(24, 16, 1);
		font-weight: 800;
		line-height: 148%;
		color:       #090528;
		@include adaptiveValue('margin-bottom', 24, 18, 1);
	}

	&__text {
		@include adaptiv-font(18, 14, 1);
		line-height: 189%;
		font-weight: 500;
		color:       #090528;
	}

	&__left {
	}

	&__right {
	}
}

