.top
	background: url('../img/top-bg.jpg') no-repeat center/cover

.top__content
	display: flex
	justify-content: space-between
	flex-wrap: wrap
	padding: 213px 0 265px 0
	@include adaptiveValue('padding-top', 213, 150, 1)
	@include adaptiveValue('padding-bottom', 265, 150, 1)


.top__left-header
	display: flex
	align-items: center
	gap: 25px
	margin: 0 0 26px

.top__left-icon
	//width: 61px
	//height: 61px
	@include adaptiveValue('width', 61, 45, 1)
	@include adaptiveValue('height', 61, 45, 1)
	display: flex
	justify-content: center
	align-items: center
	border-radius: 50%
	background: #5641FF

._icon-icon2
	//font-size: 22px
	@include adaptiv-font(22, 18, 1)
	color: #FFFFFF

.top__left-note
	//font-size: 18px
	@include adaptiv-font(18, 14, 1)
	font-weight: 500
	color: #FFFFFF

.top__title
	max-width: 497px
	font-family: 'TTFirsNeue', sans-serif
	//font-size: 54px
	@include adaptiv-font(54, 20, 1)
	font-weight: 700
	line-height: 133%
	color: #FFFFFF
	margin: 0 0 30px
	@media (max-width: 1024px)
		max-width: 726px

.top__title-img
	display: inline-block
	max-width: 100%
	//width: 53px
	//height: 53px
	@include adaptiveValue('width', 53, 25, 1)
	@include adaptiveValue('height', 53, 25, 1)
	//margin: 0 0 0 20px
	@include adaptiveValue('margin-left', 20, 10, 1)

.top__subtitle
	max-width: 497px
	font-family: 'TTFirsNeue', sans-serif
	//font-size: 18px
	@include adaptiv-font(18, 14, 1)
	font-weight: 400
	line-height: 148%
	color: #FFFFFF
	margin: 0 0 35px

.top__buttons
	display: flex
	align-items: center
	//flex-wrap: wrap
	//gap: 40px
	@include adaptiveValue('gap', 40, 15, 1)
	@media (max-width: 320px)
		flex-wrap: wrap

.top__btn
	display: flex
	justify-content: center
	align-items: center
	font-family: 'TTFirsNeue', sans-serif
	//font-size: 18px
	@include adaptiv-font(18, 14, 1)
	font-weight: 500
	border: 2px solid transparent
	border-radius: 8px
	transition: all .4s ease 0s

.top__btn_order
	//gap: 35px
	@include adaptiveValue('gap', 35, 15, 1)
	width: 281px
	height: 69px
	color: #000000
	background: #FDD751
	@media (max-width: 420px)
		width: 50%
		height: 50px
	@media (max-width: 320px)
		width: 100%
	@media (any-hover: hover)
		&:hover
			color: #FDD751
			border-color: #FDD751
			background: transparent
			&:before
				color: #FDD751

.top__btn_more
	width: 168px
	height: 69px
	color: #FFFFFF
	background: #5641FF
	@media (max-width: 420px)
		width: 50%
		height: 50px
	@media (max-width: 320px)
		width: 100%
	@media (any-hover: hover)
		&:hover
			border-color: #5641FF
			background: transparent

.top__right
	position: relative
	max-width: 676px
	width: 100%
	height: 621px
	background: url('../img/main-bg.png') no-repeat center/contain
	@media (max-width: 1024px)
		margin: 0 auto

	@media (max-width: 420px)
		height: 500px

.top__block
	position: absolute
	width: 352px
	height: 183px
	border-radius: 19px
	background: linear-gradient(300.85deg, rgba(0, 0, 0, 0.15) 15.82%, rgba(82, 82, 82, 0.02) 67.09%)
	backdrop-filter: blur(22px)
	@supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none))
		background: linear-gradient(300.85deg, rgba(0, 0, 0, 0.15) 15.82%, rgba(82, 82, 82, 0.02) 67.09%)
		-webkit-backdrop-filter: blur(2em)
		backdrop-filter: blur(2em)

	//padding: 34px 25px 34px 39px
	@include adaptiveValue('padding-top', 34, 15, 1)
	@include adaptiveValue('padding-right', 25, 15, 1)
	@include adaptiveValue('padding-bottom', 34, 15, 1)
	@include adaptiveValue('padding-left', 39, 15, 1)
	@media (max-width: 420px)
		width: 65%
		height: auto
	@media (max-width: 320px)
		width: 80%

.top__block_one
	top: 122px
	left: -128px
	@media (max-width: $md3+px)
		left: -34px
	@media (max-width: 420px)
		top: 120px
		left: 125px
	@media (max-width: 320px)
		top: 115px
		left: 95px

.top__block-title
	//font-size: 19px
	@include adaptiv-font(19, 13, 1)
	line-height: 136%
	font-weight: 800
	color: #FFFFFF
	//margin: 0 0 20px
	@include adaptiveValue('margin-bottom', 17, 10, 1)

.top__block-text
	font-weight: 500
	//font-size: 13px
	@include adaptiv-font(13, 11, 1)
	line-height: 166.5%
	color: #FFFFFF

.top__block_two
	bottom: 55px
	right: 137px
	@media (max-width: 420px)
		bottom: 70px
		right: 60px
	@media (max-width: 320px)
		bottom: 85px
		right: 50px
