.systems {

	&__content {
		padding: 75px 0 0 0;
	}

	&__top {
		display: flex;
		justify-content: center;
		align-items: center;
		//gap: 46px;
		@include adaptiveValue('gap', 46, 15, 1);
		@media (max-width: 420px) {
			justify-content: space-between;
		}
	}

	&__title {
		font-family: 'TTFirsNeue', sans-serif;
		font-weight: 600;
		//font-size: 43px;
		@include adaptiv-font(43, 18, 1);
		line-height: 133%;
		color: #07041C;
		@media (max-width: 420px) {
			max-width: 290px;
		}
	}

	&__img {
		@include adaptiveValue('width', 73, 50, 1);
		@include adaptiveValue('height', 73, 50, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 15px;
		background: linear-gradient(180deg, rgba(8, 5, 35, 0.06) 0%, rgba(9, 6, 38, 0.05) 100%);

		img {
			display: block;
			max-width: 100%;
			width: 30px;
		}

		@media (max-width: 768px) {
			flex: 0 0 50px;
			height: 50px;
		}
	}

	&__items {
		position: relative;
		top: 120px;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		column-gap: 46px;
		//row-gap: 57px;
		@include adaptiveValue('row-gap', 57, 20, 1);
		@media (max-width: $md3+px) {
			display: grid;
			grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
		}
		@media (max-width: 420px) {
			top: 80px;
		}
	}

	&__item {
		max-width: 444px;
		width: 100%;
		//height: 225px;
		height: 100%;
		display: flex;
		align-items: center;
		border: 3px solid rgba(255, 255, 255, 0.66);
		border-radius: 19px;
		overflow: hidden;
		background: linear-gradient(183.52deg, rgba(255, 255, 255, 0.82) -7.73%, #FFFFFF 99.42%);
		backdrop-filter: blur(108px);
		transition: all .4s ease 0s;
		@media (max-width: $md3+px) {
			width: 100%;
			background: #FFFFFF;
			backdrop-filter: none;
			box-shadow: -10px -10px 15px rgba(255, 255, 255, 0.5),
			10px 10px 15px rgba(70, 70, 70, 0.12),
			inset -10px -10px 15px rgba(255, 255, 255, 0.5),
			inset 10px 10px 15px rgba(70, 70, 70, 0.12);
		}
		@media (max-width: 420px) {
			//height: 150px;
		}
		@media (max-width: 320px) {
			//height: 120px;
		}
		@media (any-hover: hover) {
			&:hover {
				transform: translateY(-5px);
			}
		}
	}

	&__item-img {
		display: flex;
		justify-content: center;
		align-items: center;
		//max-width: 205px;
		//width: 47%;
		height: 100%;
		padding: 62px 58px 63px 46px;
		padding-top: 10%;

		@media (max-width: $md3+px) {
			padding: 50px 40px;
		}

		@media (max-width: 420px) {
			//max-width: 50%;
			width: 50%;
		}

		img {
			@media (max-width: 420px) {
				//max-width: 30%;
				width: 100%;
			}
		}
	}

	&__item-img--img1 {
		background: linear-gradient(270deg, rgba(116, 116, 116, 0.0297) -10%, rgba(255, 129, 87, 0.1914) 100%);
	}

	&__item-img--img2 {
		background: linear-gradient(270deg, rgba(183, 183, 183, 0.0429) -10%, rgba(239, 65, 115, 0.1485) 100%);
	}

	&__item-img--img3 {
		background: linear-gradient(270deg, rgba(0, 0, 0, 0) -7.34%, rgba(156, 205, 42, 0.0957) 41.62%, rgba(239, 167, 36, 0.1221) 100%);
	}

	&__item-img--img4 {
		background: linear-gradient(270deg, rgba(116, 116, 116, 0.0297) -10%, rgba(0, 188, 212, 0.1551) 100%);
	}

	&__item-img--img5 {
		background: linear-gradient(270deg, rgba(116, 116, 116, 0.0297) -10%, rgba(0, 188, 212, 0.1551) 100%);
	}

	&__item-img--img6 {
		background: linear-gradient(270deg, rgba(0, 0, 0, 0) -7.34%, rgba(156, 205, 42, 0.0957) 41.62%, rgba(239, 167, 36, 0.1221) 100%);
	}

	&__info {
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		//@media (max-width: $md3+px) {
		//	padding: 10px 0;
		//}
	}

	&__note {
		font-weight: 700;
		font-size: 20px;
		@include adaptiv-font(20, 13, 1);
		color: #090528;
		&:not(:last-child) {
			//margin-bottom: 30px;
			@include adaptiveValue('margin-bottom', 30, 15, 1);
			}
	}
}
