.questions {
	background: url('../img/bottom-bg.jpg') no-repeat center/cover;

	&__content {
		//padding: 254px 0 142px 0;
		@include adaptiveValue('padding-top', 254, 200, 1);
		@include adaptiveValue('padding-bottom', 142, 100, 1);

	}

	&__top {
		display: flex;
		justify-content: center;
		align-items: center;
		gap: 46px;
		margin: 0 0 100px;
	}

	&__title {
		font-family: 'TTFirsNeue', sans-serif;
		font-weight: 600;
		//font-size: 43px;
		@include adaptiv-font(43, 20, 1);
		line-height: 133%;
		color: #FFFFFF;
	}

	&__img {
		//width: 73px;
		//height: 73px;
		@include adaptiveValue('width', 73, 50, 1);
		@include adaptiveValue('height', 73, 50, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 15px;
		background: #131023;

		img {
			display: block;
			max-width: 100%;
			width: 30px;
		}
	}

	&__body {
		display: flex;
		justify-content: center;
		//gap: 40px;
		@include adaptiveValue('gap', 40, 15, 1);
		margin: 0 0 40px;
		@media (max-width: 420px) {
			flex-wrap: wrap;
		}
	}

	&__body-left {
		position: relative;
		top: 0;
		right: 0;
		max-width: 692px;
		width: 100%;
		height: 463px;
		border-radius: 27px;
		background: linear-gradient(180deg, rgba(14, 10, 38, .228) 0%, rgba(14, 10, 38, .176) 100%);
		backdrop-filter: blur(115px);
		//padding: 62px 59px 68px 59px;
		@include adaptiveValue('padding-top', 62, 15, 1);
		@include adaptiveValue('padding-right', 59, 15, 1);
		@include adaptiveValue('padding-bottom', 68, 15, 1);
		@include adaptiveValue('padding-left', 59, 15, 1);
		transition: all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				background: linear-gradient(360deg, rgba(255, 255, 255, 0.86) -7.32%, #FFFFFF 95.88%);
				backdrop-filter: blur(108px);
				.questions__body-title {
					color: #000000;
				}
			}
		}
		//@media (max-width: $md3+px) {
		//	height: 100%;
		//	box-shadow: -10px -10px 5px rgba(14, 10, 38, .228),
		//	10px 10px 5px rgba(70, 70, 70, 0.12);
		//}

	}

	&__body-icon {
		//width: 74px;
		//height: 74px;
		@include adaptiveValue('width', 74, 60, 1);
		@include adaptiveValue('height', 74, 60, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background: rgba(255, 49, 173, .18);
		margin: 0 0 30px;

		&:before {
			//font-size: 22px;
			@include adaptiv-font(22, 18, 1);
			color: #FF31AD;
		}
	}

	&__body-icon--left {
		//width: 74px;
		//height: 74px;
		@include adaptiveValue('width', 74, 60, 1);
		@include adaptiveValue('height', 74, 60, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background: #3FBC62;
		box-shadow: 0 32px 93px rgba(63, 188, 98, .14);

		&:before {
			//font-size: 22px;
			@include adaptiv-font(22, 18, 1);
			color: #FFFFFF;
		}
	}

	&__body-title {
		font-weight: 800;
		//font-size: 24px;
		@include adaptiv-font(24, 18, 1);
		line-height: 148%;
		color: #FFFFFF;
		margin: 0 0 32px;
	}

	&__body-text {
		font-weight: 500;
		//font-size: 18px;
		@include adaptiv-font(18, 14, 1);
		line-height: 166.5%;
		color: #3F3A5E;
	}

	&__body-title--left {
		color: #FFFFFF;
	}

	&__body-right {
		position: relative;
		top: 0;
		right: 0;
		max-width: 692px;
		width: 100%;
		height: 463px;
		border-radius: 19px;
		background: linear-gradient(180deg, rgba(14, 10, 38, 0.228) 0%, rgba(14, 10, 38, 0.176) 100%);
		backdrop-filter: blur(115px);
		//padding: 62px 59px 68px 59px;
		@include adaptiveValue('padding-top', 62, 15, 1);
		@include adaptiveValue('padding-right', 59, 15, 1);
		@include adaptiveValue('padding-bottom', 68, 15, 1);
		@include adaptiveValue('padding-left', 59, 15, 1);
		transition: all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				color: #000000;
				background: linear-gradient(360deg, rgba(255, 255, 255, 0.86) -7.32%, #FFFFFF 95.88%);
				backdrop-filter: blur(108px);
				.questions__body-title {
					color: #000000;
				}
			}
		}
		@media (max-width: $md3+px) {
			height: 100%;
		}
	}

	&__footer {
		position: relative;
		top: 0;
		right: 0;
		width: 100%;
		border-radius: 27px;
		background: linear-gradient(180deg, rgba(14, 10, 38, .24) 0%, rgba(14, 10, 38, 0) 121.31%);
		backdrop-filter: blur(82px);
		//padding: 53px 59px 65px 59px;
		@include adaptiveValue('padding-top', 53, 15, 1);
		@include adaptiveValue('padding-right', 59, 15, 1);
		@include adaptiveValue('padding-bottom', 65, 15, 1);
		@include adaptiveValue('padding-left', 59, 15, 1);
		transition: all .4s ease 0s;
		@media (any-hover: hover) {
			&:hover {
				color: #000000;
				background: linear-gradient(360deg, rgba(255, 255, 255, 0.86) -7.32%, #FFFFFF 95.88%);
				backdrop-filter: blur(108px);
				.questions__footer-title {
					color: #000000;
				}
			}
		}
	}

	&__footer-top {
		display: flex;
		align-items: center;
		//gap: 42px;
		@include adaptiveValue('gap', 42, 15, 1);
		margin: 0 0 39px;
		width: 100%;
		@media (max-width: 420px) {
			flex-wrap: wrap;
		}
	}

	&__footer-icon {
		@include adaptiveValue('width', 74, 60, 1);
		@include adaptiveValue('height', 74, 60, 1);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: 50%;
		background: #336CFF;
		box-shadow: 0 32px 93px rgba(81, 129, 255, .25);

		&:before {
			//font-size: 22px;
			@include adaptiv-font(22, 18, 1);
			color: #FFFFFF;
		}
	}

	&__footer-title {
		font-weight: 800;
		//font-size: 24px;
		@include adaptiv-font(24, 18, 1);
		line-height: 148%;
		color: #FFFFFF;
	}

	&__footer-text {
		font-weight: 500;
		//font-size: 18px;
		@include adaptiv-font(18, 14, 1);
		line-height: 166.5%;
		color: #3F3A5E;
	}
}
