* {
	padding: 0;
	margin: 0;
	border: 0;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

*,
*:before,
*:after {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

html {
	//font-size: #{$fontSize + px};
	font-size: rem($fontSizeVar);
}

html,
body {
	height: 100%;
	min-width: $minWidth;
}

body {
	line-height: 1;
	font-family: $fontFamily;
	-ms-text-size-adjust: 100%;
	-moz-text-size-adjust: 100%;
	-webkit-text-size-adjust: 100%;
}

a,
aside,
nav,
footer,
header,
section,
label,
span {
	display: block;
}


input,
button,
textarea {
	font-family: $fontFamily;
	font-size: inherit;
	outline: none;
}

input::-ms-clear {
	display: none;
}

button {
	cursor: pointer;
	background-color: inherit;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a,
a:visited {
	text-decoration: none;
}

a {
	color: inherit;
}

a:hover {
	text-decoration: none;
}

ul,ol li {
	list-style: none;
}

img {
	vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-weight: inherit;
	font-size: inherit;
}
