.terms {
	color:      #FFFFFF;
	background: linear-gradient(162.03deg, #040213 0.72%, #030112 87.27%);

	&__content {
		@include adaptiveValue('padding-top', 258, 150, 1);
		@include adaptiveValue('padding-bottom', 79, 50, 1);
	}

	&__body {
		a {
			display: inline-block;
			color:   #0000EE;
			transition:      all .4s ease 0s;
			@media (any-hover: hover) {
				&:hover {
					color: #008000;
				}
			}
		}
	}

	&__item {
		@include adaptiveValue('margin-bottom', 15, 10, 1);

		span {
			display: block;

			a {
				padding-left: 7px;
			}
		}
	}

	&__header {
		@include adaptiveValue('margin-bottom', 35, 20, 1);
	}

	&__title {
		font-family: 'TTFirsNeue', sans-serif;
		@include adaptiv-font(43, 20, 1);
		font-weight: 600;
		line-height: 133%;
	}

	&__text {
		@include adaptiv-font(18, 14, 1);
		line-height: 1.3;
		font-weight: 500;
	}

	&__body {
	}

	&__subtitle {
		@include adaptiv-font(24, 16, 1);
		font-weight: 600;
		line-height: 148%;
		@include adaptiveValue('margin-bottom', 10, 7, 1);
	}
}
